#productForm {
  display: flex;
  flex-direction: column;
}

#productForm .simpleProp {
  width: 30vw;
  height: 10vh;
  margin-top: 1vh;
  margin-left: 5%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border: 1px black solid;
  border-radius: 20px;
  /* background-color: #fa9e08; */
  color: white;
  flex-direction: row;
}

#catalogNewZone select {
  font-size: small;
  background-color: var(--color-text-variant);
  border: 1px solid black;
  color: white;
  border-radius: 1rem;
  padding: 1%;
}

#optionsButtons button {
  background-color: var(--color-text-variant);
  margin-left: 50%;
}

#productForm div label {
  margin: 0% 2%;
}
#productForm span {
  color: red;
  padding: 0%;
}

#productForm div input {
  height: 2vh;
  margin: 0% 1%;
  width: 30%;
}
.hidden {
  display: none;
}

#imgLabel {
  background-color: var(--color-text-variant);
  border: 1px solid black;
  border-radius: 10px;
  margin-top: 10%;
  padding: 1%;
  text-transform: uppercase;
  cursor: pointer;
}

#newProductImg,
#newProductSecondaryImg {
  display: flex;
  flex-direction: column;
  width: 30vw;
  height: 40vh;
  margin-top: 1vh;
  margin-left: 5%;
  align-items: center;
  justify-content: flex-start;
  border: 1px black solid;
  border-radius: 20px;
  /* background-color: #fa9e08; */
  color: white;
}

#newProductImg img,
#newProductSecondaryImg img {
  height: 70%;
  width: 80%;
  margin-top: 5%;
}
